.page__center {
  display: flex;
  flex-direction: column;
  max-width: 880px;
  flex: 1;
  margin: 0 auto;
}

@media screen and (max-width: 1023px) {
  .page__center {
    max-width: 581px;
  }
}

@media screen and (max-width: 767px) {
  .page__center {
    max-width: 282px;
  }
}
